import React, { useEffect, useState } from 'react'
import './style.css'
import * as dsqr from '../../../../assets/DS_qr.png'
import * as lockimg from '../../../../assets/lock.svg'
import * as logofulllight from '../../../../assets/DULT_logo_light.png'

import { useLocation } from "react-router-dom";

import { useAuth } from '../../../../store/authStore'

import { levelstore } from '../../../../store/levelStore';
import { instancestore } from '../../../../store/InstanceStore';
import { leveldetailslstore } from '../../../../store/leveldetailsStore';
import { useActiveScreen } from '../../../../store/ActiveScreen';
import { useActivePopup } from '../../../../store/ActivePopup';
import { userActiveLevelDetailsStore } from '../../../../store/ActiveLevelDetailsStore';
import { useLanguageStore } from '../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../store/ColourStore';

import Pie from 'components/Common/Pie';

import { useWindowDimensions } from 'components/App';
import { userActiveSubLevelDetailsStore } from '../../../../store/ActiveSubLevelDetailsStore'
import Animationcomponent from 'components/Common/Animation'


const Home = (props: any) => {

  const { height, width } = useWindowDimensions();

  const { aniout, setaniout } = props;

  const { colourObj } = useColourObjectStore();

  const { instance, activeInstanceData, loadinstance, loadactiveInstancebyID } = instancestore();
  

  const { level, loadlevelwithid } = levelstore();
  const { ActiveLevelDetails, loadActiveleveldetails } = userActiveLevelDetailsStore();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();

  const { Language, setLanguage } = useLanguageStore();

  const [spinner, setspinner] = useState(true)

  const { ActiveScreen, setActiveScreen } = useActiveScreen();
  const { setActivePopup } = useActivePopup();

  const { loadleveldetails } = leveldetailslstore();

  const navigate = (object: any) => {
    console.log(object);
    setActiveScreen("Level")
    loadleveldetails()
    loadActiveleveldetails(object.id)
    localStorage.setItem("ActiveLevel", object.id)
  }

  const search = useLocation().search;

  const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore()

  const { auth } = useAuth()

  const [completed, setcompleted] = useState(false)


  useEffect(() => {

    console.log("ActiveScreen", ActiveScreen);

    setaniout(false)

    if (activeInstanceData) {
      loadlevelwithid(activeInstanceData?.id)
    } else if (props.id) {
      loadlevelwithid(props.id)
    }

    // if (!ActiveLevelDetails) {
    //   loadActiveleveldetails(localStorage.getItem("ActiveLevel"))
    // }

    // if (!ActiveSubLevelDetails) {
    //   loadActiveSubleveldetails(localStorage.getItem("ActiveSubLevel"))
    //   setcompleted(Number(((ActiveLevelDetails) && ActiveLevelDetails.progress)) === 100 ? true : false)
    // }

    const id = new URLSearchParams(search).get('id');
    if (id) {
      console.log("======>>>", id)
      loadActiveleveldetailsbyqr(id)
      setActiveScreen("Level")
    }
    setspinner(false)

  }, [activeInstanceData])

  const islocked = () => {
    // To do need to add logic to check all the levels of unlock after are unlocaked 
    return false
  }

  const clr4lvl_title = (pct) => {
    let h = 350 - (pct * 2)
    let s = 50
    let l = 45

    let xyz = "hsl" + "(" + h + ", " + s + "%, " + l + "%" + ")"
    // console.log(xyz)

    return xyz
  }

  // const fade = (props) => {
  //   let result = (aniout ? (props + " " + "fade_out") : (props))
  //   return result
  // }

  const RenderList = (obj: any) => {
    // console.log("kannnnnnnnnnada : : :", obj);

    // calculation for animation
    var a = (obj.l_level * 0.10) + "s"

    return (
      <Animationcomponent
        className={'level' + ' ' + `id${obj.l_level}`}
        style={aniout ? { opacity: 1 } : { animationDelay: a }}
        Animationtype={"fade_out"}
        aniout={aniout}
        onClick={() => {
          setaniout(true);
          setTimeout(() => {
            if (!obj.is_locked) {
              if (!obj.GameDataLog[0]) {
                setActivePopup("QR")
              }
              else {
                navigate(obj)
              }
            }
          }, 290);
        }}>
        <div className="icons">
          <Pie percentage={obj.progress} text={false} radius={(((height * 0.1) < (width * 0.2)) ? (height * 0.1) : (width * 0.2)) / 2}
            strokewidth={(((height * 0.1) < (width * 0.2)) ? (height * 0.1) : (width * 0.2)) / 12} />

          <div className={obj.is_locked ? 'level_clue_image_wrapper shadow grayscale' : 'level_clue_image_wrapper shadow'}
            style={{ borderColor: colourObj.color_m2 }}>
            <img className='level_clue_image' src={obj.clue_image} />
          </div>

          <div className={obj.is_locked ? 'leveltype_wrapper shadow grayscale' : 'leveltype_wrapper shadow'}
            style={{ borderColor: colourObj.color_m2 }}>
            <img className='leveltype' src={obj.l_icon} />
          </div>

          {obj.is_locked ?
            <div className='level_status_wrapper shadow' style={{ borderColor: colourObj.color_m2 }}>
              <img className='levelstatus' src={lockimg} />
            </div>
            : !obj.GameDataLog[0] &&
            <div className='level_status_wrapper shadow' style={{ borderColor: colourObj.color_m2 }}>
              <img className='levelstatus_qr' src={dsqr} />
            </div>
          }

        </div>
        <div className={obj.is_locked ? "title grayscale" : "title"} style={{ color: clr4lvl_title(obj.progress) }}>
          {((Language?.value === "KANNADA") ? obj.TextData[0]?.title : obj.l_title)}
        </div>
      </Animationcomponent>
    )
  }

  return (
    <div className='levels'>
      {/* <div className="color_picker_boxes">
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_m1 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_s1 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_t1 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_m2 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_s2 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_t2 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_m3 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_s3 }} />
        </div>
        <div className="box">
          <div className="color" style={{ backgroundColor: colourObj.color_t3 }} />
        </div>
      </div> */}
      {(level) ? level.sort((obj1: any, obj2: any) => obj1.l_level - obj2.l_level).map(RenderList)
        :
        <>
          {(!activeInstanceData) &&
            <div className="unselected_gameorlevel">
              <div className="unseleveted_game_dult_logo">
                <img className='level_clue_image' src={logofulllight} />
              </div>
              <div className="unseleveted_game_text">Welcome! Please click the qr code to start playing!</div>
            </div>}
        </>
      }
    </div>
  )
}

export default Home
